import React, { Component } from 'react'
import { UserOutlined, LockOutlined, CreditCardOutlined, BellOutlined } from '@ant-design/icons';
import { Menu } from 'antd';
import { Link, Redirect, Route, Switch } from 'react-router-dom';
import InnerAppLayout from 'layouts/inner-app-layout';
import EditProfile from './EditProfile';
import { connect } from 'react-redux';
// import ChangePassword from './ChangePassword';
// import Billing from './Billing';
// import Notification from './Notification';

const SettingOption = ({ match, location, usersData }) => {
	console.log(usersData, 'SettingOption' )
	return (
		<Menu
			defaultSelectedKeys={`${match.url}/edit-profile`}
			mode="inline"
			selectedKeys={[location.pathname]}
		>
			<Menu.Item key={`${match.url}/edit-profile`}>
				<UserOutlined />
				<span>Edit Profile</span>
				<Link to={'edit-profile'} />
			</Menu.Item>
		</Menu>
	);
};

const SettingContent = ({ match,  usersData}) => {
	return (
		<Switch>
			<Redirect exact from={`${match.url}`} to={`${match.url}/edit-profile`} />
			<Route 
				path={`${match.url}/edit-profile`} 
				render={(props) => (
					<EditProfile
						userId={match.params.id} 
						{...props}
					/>
				)}
			/>
		</Switch>
	)
}

export class Setting extends Component {
	render() {
		return (
			<InnerAppLayout 
				sideContentWidth={320}
				sideContent={<SettingOption {...this.props}/>}
				mainContent={<SettingContent {...this.props}/>}
			/>
    );
	}
}

const mapStateToProps = ({users}) => {
	console.log(users, 'users')
	const {loading, usersData} = users;
	return {loading, usersData}
	
}  	
// export default Setting
export default  connect(mapStateToProps)(Setting) 